import React from 'react';
import {BrowserRouter as Router,Switch} from 'react-router-dom'
import routes from './router.config';
import Header from './myreact/Header';
import RouteWithSubRoutes from './myreact/RouteWithSubRoutes'
import PrivateRoute from './myreact/PrivateRoute'
const App = () => {
	return <Router>
		<Header />
		<Switch>
			{
				routes.map((route,i)=>{
					if(route.pri){
						return <PrivateRoute key={i} {...route} 
							render={
								props=><route.component {...props} routes={route.routes} />
							}
						/>
					}
					return (
						<RouteWithSubRoutes key={i} {...route} 
							render={
								props=><route.component {...props} routes={route.routes} />
							} 
						/>
					)
				})
			}
		</Switch>
	</Router>
}

export default App;

