import Home from './myreact/Home';
import Doc from './myreact/Doc';   
import Tuto from './myreact/Tuto';
import Blog from './myreact/Blog';
import Comm from './myreact/Comm';
import Login from './myreact/Login';
import Core from './myreact/Core'
import Guide from './myreact/Guide'
import Api from './myreact/Api'
import Hooks from './myreact/Hooks'
const routes = [
    {
        path:'/',
        exact:true,
        component: Home
    },
    {
        path:'/doc',
        component: Doc,
        pri:true,
        routes: [
            {
                path: '/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            }
        ]
    },
    {
        path:'/tuto',
        component: Tuto,
        pri:true
    },
    {
        path:'/blog',
        component: Blog
    },
    {
        path:'/comm',
        component: Comm
    },
    {
        path:'/login',
        component: Login
    }
]
export default routes;