// export default withRouter(Doc)

import React from 'react'
import './doc.css'
import {BrowserRouter as Router,Switch,Link,Redirect} from "react-router-dom";
import RouteWithSubRoutes from './RouteWithSubRoutes'
import PrivateRoute from './PrivateRoute'
const Doc = (props) => {
  // console.log(props);
    // useEffect(()=>{
    //     if(!localStorage.getItem('userinfo')){
    //         props.history.push({
    //             pathname:'/login',
    //             state:props.location
                
    //         })
    //     }
    // },[])
    return (
      <Router>
        <div className='doc'>
          <div className='content'>
            <Switch>
              {            
                props.routes.map((route, i) => {
                  if(route.pri){
                    return <PrivateRoute key={i} {...route} 
                      render={
                        props=><route.component {...props} routes={route.routes} />
                      }
                    />
                  }
                  return (
                    <RouteWithSubRoutes key={i} {...route} 
                      render={
                        props=><route.component {...props} routes={route.routes} />
                      } 
                    />
                  )
                })
              }
              <Redirect from="/doc" to='/doc/core'/>
            </Switch>
          </div>          
          <ol>
            <li><Link exact to="/doc/core">核心概念</Link></li>
            <li><Link to="/doc/guide">高级指引</Link></li>
            <li><Link to="/doc/api">API</Link></li>
            <li><Link to="/doc/Hooks">Hooks</Link></li>
          </ol> 
        </div>
      </Router>
    );
}

export default Doc