import React from "react";
import {Route} from "react-router-dom";

function RouteWithSubRoutes(route) {
    return (
        <div>
            <Route
                exact 
                path={route.path}
                render={
                    props =><route.component {...props} routes={route.routes} />
                }
            />
        </div>
    );
  }

export default RouteWithSubRoutes